body{
  font: 20px "Century Gothic", Futura, sans-serif;
  margin: -20px;
  display: flex;
justify-content: center;
height:100%;
width:100%;
margin-top: -20px;
}
.nav{
  background-color: #000;
  color:#fff;
}
input[type=text]{
  width:132px;
  border: 2px solid #08a2cc;
  border-radius: 4px;
  background-color: #000;
  color:#fff;
}
.nav-dark{
  background-color: #999;
  color:#1a1919;
}
.dark-mode{
  background-color: #1a1919;
  color:#999;
}
.lab{
  font-size: 15px;
}
.high{
  font-size:25px;

}
ol, ul {
  padding-left: 30px;
}
.Greet{
  text-align: center;
  margin-bottom: -5px;
}
.nameform{
  margin-top: -10px;
  margin-bottom: -2px;
}
.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #000;
  border: 3px solid #08a2cc;
  float: left;
  font-size: 80px;
  font-weight: bold;
  line-height: 34px;
  outline-width: 30px;
  height: 100px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  color: #999;
  width: 100px;
}

.square:not(:focus):hover{
  background-color: #cfcfcf;
}
.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}
#single{
  display:none;
}
.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}
.footer{
  text-align:center;
}


.switch {
  position: relative;
  display: inline-block;
  width: 35px;
  height: 20px;
  top:4px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 16px;
  width: 16px;
  left: 2px;
  bottom: 2px;
  top:2px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(15px);
  -ms-transform: translateX(15px);
  transform: translateX(15px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 24px;
}

.slider.round:before {
  border-radius: 50%;
}
